/* ./src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

body {
  font-family: inter;
}

.container-wrapper {
  @apply px-3 lg:px-8 xl:px-8 2xl:px-40;
}
.dashboard-wrapper {
  @apply px-3 lg:px-4 xl:px-8 2xl:px-6;
}

.btn-sm {
  @apply px-5 py-1 text-sm font-normal whitespace-pre transition duration-300 rounded-md shadow hover:bg-main hover:text-white;
}
.btn {
  @apply px-5 py-2 font-normal whitespace-pre transition duration-300 rounded-md shadow hover:bg-main hover:text-white;
}
.btn-base {
  @apply px-5 py-2 font-normal whitespace-pre transition duration-300 rounded-md shadow;
}

.btn-main {
  @apply text-white cursor-pointer bg-main hover:bg-main_hover;
}

.btn-sub {
  @apply text-white bg-sub hover:bg-sub_hover;
}
.btn-green {
  @apply text-white bg-green-600 hover:bg-green-600;
}

.btn-info {
  @apply text-white bg-info hover:bg-info_hover;
}

.line {
  @apply h-0.5 bg-gray-300;
}

.link {
  @apply px-2 py-3 text-gray-500 transition duration-300 hover:text-main;
}

.form_error_message {
  @apply text-sm text-red-500;
}
.form_success_message {
  @apply text-sm text-green-500;
}

.success {
  @apply py-5 text-center text-green-500;
}

.error {
  @apply py-5 text-center text-red-500;
}

.password_eye {
  @apply absolute text-2xl text-gray-300 cursor-pointer top-10 right-3;
}

.font-base-light {
  @apply text-base font-light font-main;
}
.font-xl-light {
  @apply text-xl font-light font-main;
}
.font-lg-light {
  @apply text-lg font-light font-main;
}
.font-sm-light {
  @apply text-sm font-light font-main;
}
.font-sm-semibold {
  @apply text-sm font-semibold font-main;
}
.font-base-semibold {
  @apply text-base font-semibold font-main;
}
.font-lg-semibold {
  @apply text-lg font-semibold font-main;
}

.font-xl-semibold {
  @apply text-xl font-semibold font-main;
}
.font-sm-bold {
  @apply text-sm font-bold font-main;
}
.font-base-bold {
  @apply text-base font-bold font-main;
}
.font-lg-bold {
  @apply text-lg font-bold font-main;
}

.font-xl-bold {
  @apply text-xl font-bold font-main;
}

.profile_menu_item {
  @apply block px-4 py-2 mt-2 text-sm font-semibold bg-transparent rounded-lg hover:bg-gray-200;
}
.form_input {
  @apply w-full px-3 py-3 mb-1 leading-tight text-gray-700 border rounded shadow-sm;
}
.form_input:focus {
  @apply shadow-sm outline-none;
}
.react-select__control {
  @apply w-full px-3 py-1 leading-tight text-gray-700 border rounded shadow-sm;
}

.custom_shadow {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
@media (max-width: 640px) {
  .gm-fullscreen-control {
    display: none;
  }
}
/* .track-color {
  background-color: green !important;
  color: #e95757 !important;
} */
.track-color::after {
  content: ".";
  color: white;
}
.job-details-title::after {
  background-image: url("./images/ROH-logo.png");
  background-size: 20px 30px;
  background-repeat: no-repeat;
  display: inline-block;
  width: 20px;
  height: 30px;
  content: "";
  position: relative;
  left: 10px;
  top: 5px;
}
.jobAddress::before {
  background-image: url("./images/golocation.png");
  background-size: 25px 25px;
  background-repeat: no-repeat;
  display: inline-block;
  width: 25px;
  height: 25px;
  content: "";
  position: relative;
  right: 2px;
  top: 5px;
}
@media (min-width: 780px) {
  .candidate-grid {
    grid-template-columns: 2fr 1fr;
  }
}

@media (min-width: 768px) {
  .job-detail-md {
    width: 44%;
  }
}

@media (max-width: 525px) {
  .hire-modal-width {
    width: 95vw;
  }
}
.track-color-1 {
  background-color: #007fee !important;
  height: 0.5rem;
  border: gainsboro solid 1px;
}

.gm-fullscreen-control {
  margin-top: 28px !important;
}

.tooltip {
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-top: 15px solid #007fee;
  position: absolute;
  /* top: 1.2rem;
  left: 0.75rem; */
}
.tooltip:hover {
  border-top: 15px solid #e95757;
}

.minHeight {
  min-height: calc(100vh - 327px);
  /* 327 height of header and footer adjusted with ipads */
}
.btnFacebook {
  width: 185px;
  height: 45px;
  background: #3b5998;
  border-radius: 2px;
  font-size: 14px;
  color: white;
  border: 0px transparent;
  text-align: center;
  margin: 5px;
  display: inline-block;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 2px 2px 0px,
    rgba(0, 0, 0, 0.24) 0px 0px 1px 0px;
}
.btnFacebook i {
  padding: 10px;
}
/* custom__scrollbar */

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
  border-radius: 5px;
}

::-webkit-scrollbar {
  width: 11px;
  background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
  background-color: #007fee;
  border: 2px solid #007fee;
  border-radius: 5px;
}
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .top-button.btn {
    @apply px-2 !important;
  }
}

/* custom__scrollbar */

.parent-scroll {
  height: 600px;
  border: 1px solid red;
  overflow-y: auto;
}

#scrolling-container {
  border: 1px solid black;
}

.ql-clipboard {
  position: fixed !important;
}

.ql-container {
  height: auto !important;
}

.ql-editor {
  height: auto !important;
}
